@import "../../mediaqueries.scss";
@import "../../theme.scss";

.playlist {
  height: 100%;
  position: relative;

  .new-playlist {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 66px;
    padding: 0 15px;
    background: rgba(255, 255, 255, 1);
    border-radius: 3px;
    border: 1px solid rgba(235, 235, 235, 1);
    display: flex;

    .new-playlist-btn {
      align-self: center;

      button {
        padding: 0 20px;
        height: 40px;
        background: $bg-color;
        border-radius: 3px;
        border: 0;
        font-size: 14px;
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        line-height: 20px;
        cursor: pointer;
      }
    }

    .playlist-num {
      align-self: center;
      margin-left: 50px;

      .playlist-num-icon {
        height: 100%;
        display: inline-block;
        font-size: 32px;
        vertical-align: middle;
        margin-right: 10px;
        padding-top: 5px;
        i {
          font-size: 32px;
        }
      }

      .num-text {
        float: right;
        height: 100%;
        padding: 5px 3px 0 3px;
        text-align: left;

        .text {
          margin: 0;
          height: 19px;
          font-size: 13px;

          font-weight: 400;
          color: rgba(1, 1, 1, 0.6);
          line-height: 19px;
        }

        .num {
          margin: 0;
          height: 24px;
          font-size: 16px;

          font-weight: 400;
          color: rgba(25, 25, 25, 1);
          line-height: 24px;
        }
      }
    }
  }

  .playlist-operation {
    position: absolute;
    top: 66px;
    left: 0;
    right: 0;
    display: flex;
    height: 45px;
    background: rgba(255, 255, 255, 1);
    border-radius: 3px;
    border: 1px solid rgba(235, 235, 235, 1);
    padding: 0 15px;

    .checkAll {
      align-self: center;
      display: flex;
      align-items: center;

      .check-frame {
        margin-right: 5px;
        cursor: pointer;
        border-radius: 5px;
        font-size: 20px;
      }
      .icon-xuanzhong1{
        color: $bg-color;
      }

      .checkAll-text,.invert-Selection {
        color: rgba(2, 2, 2, 1);
        line-height: 20px;
        margin-right: 24px;
      }
      .invert-Selection {
        cursor: pointer;
        color: $bg-color;
      }
    }

    .playlist-delete {
      align-self: center;
      padding-left: 24px;
      border-left: 1px solid #e9e9e9;

      span {
        display: block;
        height: 30px;
        padding: 0 10px;
        border-radius: 3px;
        border: 1px solid rgba(191, 191, 191, 1);
        font-weight: 400;
        color: rgba(31, 31, 31, 1);
        line-height: 30px;
        cursor: pointer;

        i {
          margin-right: 10px;
        }
      }
    }

    .playlist-sort {
      position: relative;
      height: 30px;
      align-self: center;
      border-left: 1px solid #e9e9e9;
      padding-left: 24px;
      margin-right: 24px;
      display: flex;

      .sort {
        align-self: center;
        border-radius: 3px;
        border: 1px solid rgba(191, 191, 191, 1);
        padding: 0 10px;
        display: block;
        height: 100%;
        line-height: 30px;
        cursor: pointer;

        i {
          margin-left: 10px;
        }

        &:hover {
          background: $hover;
        }
      }

      .sort-list {
        width: 250px;
        position: absolute;
        top: 31px;
        right: 0;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.15);
        border-radius: 3px;
        border: 1px solid rgba(233, 233, 233, 1);
        padding: 12px 0;
        z-index: 999;

        ul li {
          cursor: pointer;
        }

        ul li span {
          display: block;
          height: 30px;
          font-size: 14px;

          color: rgba(2, 2, 2, 1);
          line-height: 30px;
          padding-left: 15px;

          i {
            visibility: hidden;
            margin-right: 15px;
          }

          .check-icon {
            visibility: initial;
            color: $bg-color;
          }
        }

        ul li:hover {
          background: $hover;
        }
      }
    }
  }

  .playlist-main {
    height: 100%;
    padding-top: 111px;

    .playlist-main-box {
      display: flex;
      flex-wrap: wrap;
      align-content: flex-start;
      min-height: 100%;
      height: 100%;
      overflow-y: auto;
      padding-bottom: 135px;

      .list {
        display: flex;
        padding: 5px 10px;
      }

      .playlist-list {
        flex: 1;
        position: relative;
        display: flex;
        flex-direction: column;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 2px 6px 0px rgba(210, 222, 241, 1);
        border: 2px solid rgba(255, 255, 255, 1);
        border-radius: 4px;
        cursor: pointer;

        .top-img {
          height: 55%;
          display: flex;
          flex-direction: column;
          .img1 {
            height: 67%;
            background-color: #f3f5f7;
            background-repeat: no-repeat;
            background-position: center;
            margin-bottom: 2px;
            text-align: center;
            position: relative;
            overflow: hidden;

            img {
              position: absolute;
              top: 50%;
              left: 50%;
              display: block;
              min-width: 100%;
              min-height: 100%;
              transform: translate(-50%, -50%);
            }
          }

          .img2-4 {
            flex: 1;
            display: flex;
            justify-content: space-around;

            .img2,
            .img3,
            .img4 {
              flex: 30%;
              background-repeat: no-repeat;
              background-color: #f3f5f7;
              background-position: center center;
              position: relative;
              overflow: hidden;

              img {
                position: absolute;
                top: 50%;
                left: 50%;
                display: block;
                min-width: 100%;
                min-height: 100%;
                transform: translate(-50%, -50%);
              }
            }

            .img3 {
              margin: 0 1px;
            }
          }
        }

        .playlist-information {
          height: 45%;
          display: flex;
          flex-direction: column;

          .p-i-top {
            position: relative;
            padding: 0 14px;
            height: 50%;

            .playlist-name {
              width: 140px;
              height: 19px;
              font-size: 14px;
              color: rgba(0, 0, 0, 1);
              line-height: 19px;
              margin: 11px 0;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }

            .update-time {
              overflow: hidden;
              // margin-bottom: 10px;

              span {
                display: block;
                float: left;
                height: 21px;
                background: rgba(243, 243, 243, 1);
                border-radius: 3px;
                font-size: 12px;

                font-weight: 400;
                color: rgba(144, 144, 144, 1);
                line-height: 19px;
                padding: 0 7px;
              }
            }

            .frames_def {
              position: absolute;
              top: 12px;
              right: 14px;
              display: block;
              width: 44px;
              height: 18px;
              background: $bg-color;
              border-radius: 3px;
              font-size: 12px;
              color: white;
              line-height: 18px;
              text-align: center;
            }
          }

          .p-i-bottom {
            height: 50%;
            padding: 15px 0;
            margin: 0 11px;
            display: flex;
            border-top: 1px solid #e7e7e7;

            .photo-num,
            .frame-num {
              padding: 0 5px;
              text-align: center;
            }

            .more {
              margin-left: auto;
              position: relative;

              span {
                font-size: 20px;
                color: #000;
                padding: 0 3px;
                border-radius: 3px;

                &:hover {
                  background-color: $hover;
                }
              }

              .rename {
                position: absolute;
                display: none;
                top: 33px;
                right: -20px;
                width: 180px;
                background: rgba(255, 255, 255, 1);
                box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.15);
                border-radius: 3px;
                padding: 12px 0;
                border: 1px solid rgba(233, 233, 233, 1);
                z-index: 9;

                ul li span {
                  height: 42px;
                  line-height: 42px;
                  font-size: 14px;
                  color: rgba(2, 2, 2, 1);

                  i {
                    font-size: 20px;
                    margin: 0 16px;
                    vertical-align: middle;
                  }
                }

                ul li:hover {
                  background-color: $hover;
                }
              }
            }

            .num1 {
              height: 20px;
              font-size: 14px;
              font-weight: 500;
              color: rgba(25, 25, 25, 1);
              line-height: 20px;
            }

            .text1 {
              height: 19px;
              font-size: 13px;

              font-weight: 400;
              color: rgba(25, 25, 25, 1);
              line-height: 19px;
              opacity: 0.3;
            }
          }
        }

        &:hover {
          border: 2px solid $bg-color;
        }

        &:hover .icon-check-s {
          visibility: initial;
        }
      }

      .playlist-list-check {
        border: 2px solid $bg-color;

        .icon-check-s {
          visibility: initial;
          border: 0;
        }
      }
    }

    p {
      margin: 0;
    }
  }
}