@media (max-width: 1110px) {
  #page {
    header {
      .logo {
        display: none;
      }

      .nav {
        display: none;
      }
    }

    .home {
      .banner-img {
        position: absolute;
        width: 300px;
        height: 196px;
        right: 45px;
        bottom: 0;
      }
    }
  }
}

@media (min-width: 1110px) {
  #page {
    header {
      .menu-sidebar {
        display: none;
      }
    }
  }
}

/*<=950的设备*/
@media (max-width: 950px) {
  #page {
    .overall-data .overview-right {
      flex: 1;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;

      >div:nth-child(3) {
        display: none;
      }

      >div:nth-child(4) {
        display: none;
      }
    }

    .download-frames .overview-inner .home-frame {
      flex: 1;

      >div:nth-child(2) {
        display: none;
      }
    }
    .playlistdetails .Time{
      display: none;
    }
  }

  .home .apple {
    a {
      width: 100px !important;
    }

    canvas {
      width: 80px !important;
      height: 80px !important;
    }
  }

  .container {
    .login-mod .login-inner {
      left: 30% !important;
    }
  }
}

/*>=950的设备*/
@media (min-width: 951px) {
  #page {
    .playlistdetails .playlist-set{
      display: none;
    }
  }
}

/*<=1220的设备*/
@media (max-width: 1220px) {
  #page {
    .home {
      width: 100%;

      .bindframe-btn::after,
      .bindframe-btn::before {
        width: 0;
      }

      .bindframe-btn {
        margin-left: 40px;
      }

      .overall-data,
      .download-frames {
        >div:first-child {
          margin-right: 10px;
        }

        .playlist-data {
          width: 48%;

          .playlist-data-l {
            width: 48%;
          }

          .playlist-data-r .playlist-data-r-img {
            width: 43%;
            height: 75px;
            margin-right: 10px;
          }
        }

        .album-data,
        .rahmen-data {
          width: 23%;
        }

        .playlist-data,
        .album-data,
        .rahmen-data {
          p:nth-child(1) {
            margin-top: 15px;
          }

          p:nth-child(2) {
            margin: 15px 0 15px 0;
          }

          p:nth-child(3) {
            margin-bottom: 15px;
          }
        }

        .apple {
          margin: 0;
        }
      }
    }


    .Albumdetails,
    .Rahmendetails {

      .a-details-information,
      .Rahmendetails-information {

        .file-num,
        .frame-num,
        .creation-time,
        .last-update,
        .Album-file-num,
        .Album-creation-time,
        .Album-last-update {
          display: none;
        }
      }

      .dialog {
        .p_video {
          width: 495px !important;
          height: 500px !important;
        }

        .img-wrap {
          img {
            max-height: 400px;
            max-width: 420px;
          }
        }

        .onPiece {
          left: 0;

          span {
            background-color: $bg-color;
          }
        }

        .nextPage {
          right: 0;

          span {
            background-color: $bg-color;
          }
        }
      }
    }
  }
}

/*<=1200的设备*/

@media (min-width: 1221px) {
  #page {


    .Albumdetails,
    .rahmendetails {


      .a-details-information,
      .Rahmendetails-information {

        .playlist-msg-icon,
        .Album-info-btn,
        .rahmen-info {
          display: none;
        }
      }
    }
  }
}

/*>=1201的设备*/

@media (min-width: 1367px) {
  #page {
    header {
      .logo {
        display: block;
      }
    }
  }
}

